@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap);
body {
    font-family: 'Poppins', sans-serif;
  }
  
  .flex{
    display: flex;
  }
  .container {
    max-width: 500px;
    margin: 30px auto;
    overflow: auto;
    min-height: 300px;
    border: 1px solid steelblue;
    padding: 30px;
    border-radius: 5px;
  }
  .blueBorder{
    
    border: 1px solid steelblue;
  }
  .blueBorder-flex{
    display: flex;
    align-items: center;
    border: 1px solid steelblue;
  }
  
  .btn{
    
    border: none;
    padding: 10px;
    margin: 5px;
    border-radius: 10px;
    cursor: pointer;
    font-family: inherit;
    background-color: black;
    color:white;
  }
  .btn:active{
    -webkit-transform: scale(0.98);
            transform: scale(0.98);
  }
  .item{
    background-color: #F4F4F4;
    padding: 5px 10px;
    margin:10px 0px;
  }
  .item h3{
    
    align-items: center;
    justify-content: space-between;
  }

  .form-control{
    margin: 10px;
  }
  .form-control label{
    display:block;
  }
  .align-right{
    color:red;
    float:right;
    padding:5px;
    cursor: pointer;
    font-size: 20px;
  }
  form{
    background-color: #F4F4F4;
    padding: 10px;
  }
  .form-control input {
    width: 90%;
    height: 40px;
    margin: 5px;
    padding: 3px 7px;
    font-size: 17px;
  }

  header .btn {
    width: 100%;
  }

  .d-flex{
    display: flex;
    align-items: center;
  }

.w-33 {
  width: 33%;
  text-align: center;
}

.text-align-right{
  display: block;
  text-align: right;
}

.text-align-left{
  text-align: left;
}

.justify-between{
  justify-content: space-between;
}

.text-align-center{
text-align: center;
}
